import { Component } from '@angular/core';
import {WindowRefService} from './services/window.service';
import { UtilsService} from './services/utils.service';
import {userAuthKeyUrl, userKeyUrl} from './app.constants';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  public userID: number;
  public authKey: string;
  constructor(windowRef: WindowRefService, utils: UtilsService) {
    const a2ajx = <any>windowRef.nativeWindow.location.search.replace(/^\?/, '');
    this.userID = a2ajx[userKeyUrl];
    this.authKey = a2ajx[userAuthKeyUrl];
  }
}



